.home__page-section {
	display: flex;
	justify-content: center;
	color: #fff;
	padding: 80px 0;
	background-color: #1c2237;
}

.home__hero-row {
	align-items: center;
}

.row {
	display: flex;
	margin-right: -15px;
	margin-bottom: -15px;
	margin-left: -15px;
	flex-wrap: wrap;
	align-content: stretch;
}

.col {
	margin-bottom: 15px;
	padding-right: 15px;
	padding-left: 15px;
	flex: 1;
	max-width: 50%;
	flex-basis: 50%;
}

.home__hero-text-wrapper {
	max-width: 540px;
	padding-top: 0;
	padding-bottom: 60px;
}

.top-line {
	color: #f00946;
	font-size: 15px;
	line-height: 16px;
	font-weight: 500;
	letter-spacing: 1.4px;
	text-transform: uppercase;
	margin-bottom: 20px;
}

.heading {
	margin-bottom: 24px;
	font-size: 48px;
	line-height: 1.1;
	font-weight: 600;
	color: #f7f8fa;
}

.dark {
	color: #1c2237;
}

.darkBg {
	background-color: #1c2237;
}

.home__hero-subtitle {
	max-width: 440px;
	margin-bottom: 35px;
	font-size: 18px;
	line-height: 24px;
}

.home__hero-img-wrapper {
	max-width: 555px;
}

.home__hero-img {
	max-width: 100%;
	padding-right: 0;
	margin: 0 0 0 10px;
}

.home__hero-img-shadow {
	max-width: 100%;
	padding-right: 0;
	margin: 0 0 0 10px;
	box-shadow: 0 8px 6px -6px black;
}

.img {
	border: 0;
	max-width: 100%;
	vertical-align: middle;
	display: inline-block;
}

@media screen and (max-width: 991px) {
	.home__page-section {
    padding: 10px;
  }
  
  .container {
		padding-right: 30px;
		padding-left: 30px;
	}

	.home__hero-img {
		margin: 0;
	}

	.home__hero-img-shadow {
		margin: 0;
	}
	
}

@media screen and (max-width: 768px) {
  .home__page-section {
    padding: 0px;
  }

	.home__hero-text-wrapper {
		padding-bottom: 65px;
	}

	.col {
		max-width: 100%;
		flex-basis: 100%;
	}
}